* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  /* sizes */
  --index: calc(1vw + 1vh);
  --font-size: 16px;
  /* SPACES: 2, 4, 8, 12, 16, 24, 32, 48, 64, 80, 96, 128  */

  /* main colors */
  --color-background: #ebfbee;
  --color-text-primary: #181818;
  --color-title: #ebfbee;

  /* slider */
  --slider-width: 75vw;
  --slider-height: 50svh;
  --slider-border-radius: 8px;
  --slider-box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  --slider-btn-width: 2.5rem;
  --slider-btn-height: 2.5rem;
  --slider-btn-color: #333;
  --slider-btn-color-bg: rgba(255, 255, 255, 0.7);
  --slider-btn-font-size: 1.7rem;
  --slider-btn-z-index: 10;
  --slider-dot-height: 0.7rem;
  --slider-dot-width: 0.7rem;
  --slider-dot-margin-right: 1.75rem;
  --slider-dot-color: #b9b9b9;
  --slider-dot-color-active: #fff;
}

body {
  background-color: var(--color-background);

  /* font */
  font-family: "Roboto", sans-serif;
  font-size: var(--font-size);
  font-weight: 400;
  color: var(--color-text-primary);
  font-style: normal;
  line-height: 1.2;
}

h1 {
  font-size: 2.986rem;
  font-weight: 700;
}

h2 {
  font-size: 2.488rem;
  font-weight: 600;
}

/* HEADER */

.title {
  color: var(--color-title);
  text-align: center;
  font-family: "Cabin", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  text-shadow: 2px 2px 8px #000000;
}

.header-container {
  position: relative;
}

.header-container::after {
  content: "";
  position: absolute;
  z-index: 100;
  width: 100%;
  height: calc(var(--index) * 18);
  background-image: url("../img/after-header.webp");
  background-size: cover;
  background-repeat: no-repeat;
  bottom: calc(var(--index) * -8);
}

.layers {
  height: 100vh;
  overflow: hidden;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.layer {
  position: absolute;
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: center;
  /* hardware priority to transform */
  will-change: transform;
  z-index: 2;
}

.layer__base {
  background-image: url("../img/layer-base.webp");
  /* translate3d(X, Y, Z */
  transform: translate3d(0, calc(var(--scrollTop) / 1.5), 0);
  z-index: 0;
}

.layer__middle {
  background-image: url("../img/layer-middle.webp");
  transform: translate3d(0, calc(var(--scrollTop) / 2.5), 0);
}

.layer__front {
  background-image: url("../img/layer-front.webp");
  transform: translate3d(0, calc(var(--scrollTop) / 8), 0);
}
.layer__header {
  z-index: 1;
  transform: translate3d(0, calc(var(--scrollTop) / 1.2), 0);
  /* mix-blend-mode: hard-light; */
}

.layer__caption {
  margin-top: -15vh;
}

.layer__title {
  letter-spacing: calc(var(--index) / 4);
  text-transform: uppercase;
  font-size: calc(var(--index) * 1.5);
}

.layer__sub-title {
  letter-spacing: calc(var(--index) / 3);
  font-size: calc(var(--index) * 1.4);
}

/* SECTION */

.section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* SLIDER */

/*//////////
// SLIDER //
//////////*/

.noSelect {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.noSelect:focus {
  outline: none !important;
}

.slider {
  width: var(--slider-width);
  height: var(--slider-height);
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  border-radius: var(--slider-border-radius);
  box-shadow: var(--slider-box-shadow);
}

.slide {
  position: absolute;
  top: 0;
  width: 100%;
  height: var(--slider-height);

  display: flex;
  align-items: center;
  justify-content: center;

  transition: transform 1s;
}

.slide > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slider__btn {
  position: absolute;
  top: 50%;
  z-index: var(--slider-btn-z-index);

  border: none;
  background: var(--slider-btn-color-bg);
  font-family: inherit;
  color: var(--slider-btn-color);
  border-radius: 50%;
  height: var(--slider-btn-height);
  width: var(--slider-btn-width);
  font-size: var(--slider-btn-font-size);
  cursor: pointer;
}

.slider__btn--left {
  left: 8%;
  transform: translate(-50%, -50%);
}

.slider__btn--right {
  right: 8%;
  transform: translate(50%, -50%);
}

.dots {
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}

.dots__dot {
  border: none;
  background-color: var(--slider-dot-color);
  opacity: 0.7;
  height: var(--slider-dot-height);
  width: var(--slider-dot-width);
  border-radius: 50%;
  margin-right: var(--slider-dot-margin-right);
  cursor: pointer;
  transition: all 0.5s;

  /* Only necessary when overlying images */
  /* box-shadow: 0 0.6rem 1.5rem rgba(0, 0, 0, 0.7); */
}

.dots__dot:last-child {
  margin: 0;
}

.dots__dot--active {
  background-color: var(--slider-dot-color-active);
  opacity: 1;
}
